.container-favorite-recipes {
  background-color: #FFF4F4;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  min-height: 100vh;
  margin: 20px;
}

.filters-button-recipe-button {
  width: 90%;
  display: flex;
  justify-content: space-between;
}

.filters-button-recipe-button button {
  width: 85px;
  height: 40px;
  background: #FFFFFF;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  border: none;
  font-size: 18px;
  color: #FF6060;
  text-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
}

.section-title {
  position: absolute;
  left: 31.4%;
  top: 8em;
}

.content-favorite-recipes {
  margin-top: 4em;
}

@media only screen and (min-width: 700px) {
  .content-favorite-recipes {
    width: 36.6%;
  }
}