.container-explore-origin {
  background-color: #FFF4F4;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  padding-bottom: 50px;
}

.label-origin {
  width: 90%;
}

.select-origin {
  background: #FFFFFF;
  background-image: url(/recipe-app/static/media/expandArrow.13edb5f7.svg);
  background-repeat: no-repeat;
  background-position-x: 98%;
  background-position-y: 16px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  margin-top: 0.8em;
}

.select-origin select {
  width: 100%;
  height: 40px;
  border: none;
  padding: 0 10px;
  -webkit-appearance: none;
  -moz-appearance: none;
  background: transparent;
  font-weight: bold;
  font-size: 14px;
}

@media only screen and (min-width: 500px) {
  .label-origin {
    width: 65%;
  }

  .select-origin {
    background-position-x: 97%;
  }
}

@media only screen and (min-width: 900px) {
  .label-origin {
    width: 65%;
  }

  .select-origin {
    background-position-x: 99%;
  }
}
body {
  background-color: #FF6060;
}

.container {
  align-items: center;
  display: flex;
  flex-direction: row;
  height: 100vh;
  justify-content: center;
  width: 100vw;
}

.container-form {
  align-items: center;
  display: flex;
  flex-direction: column;
  width: 60%;
}

.form-class {
  display: flex;
  flex-flow: column;
}

.form-class input {
  border: 0px solid rgba(0, 0, 0, 0);
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.25);
  margin-bottom: 14px;
}

.link-enable {
  border: 0px solid rgba(0, 0, 0, 0);
  border-radius: 1em;
  display: flex;
  justify-content: center;
  margin: auto;
  margin-top: 2em;
}

.real-button-enable {
  border: 0px solid rgba(0, 0, 0, 0);
  border-radius: 1em;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.25);
  font-size: 18px;
  width: 10em;
}

.button-enable {
  border: 0px solid rgba(0, 0, 0, 0);
  border-radius: 1em;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.25);
  display: flex;
  font-size: 18px;
  justify-content: center;
  margin-top: 2em;
  width: 10em;
}

.title-login {
  align-items: center;
  display: flex;
  flex-flow: column;
  margin-bottom: 2em;
  text-align: center;
}

.title-login h1 {
  font-family: Oregano;
  font-size: 60px;
  color: white;
}

@media only screen and (max-width: 500px) {
  .title-login h1 {
    font-size: 40px;
  }

  .title-login img {
    width: 12em;
  }
}

@media only screen and (max-height: 500px) {
  .title-login h1 {
    font-size: 40px;
  }

  .title-login img {
    width: 12em;
  }
}
